.content_contact{
    height: 650px;
    @include verticalCenter;


    .content{
        border-radius: 10px;
        height: 390px;
        width: 560px;
        background-color: $grey;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 15% 1fr 15%;
        grid-template-areas: 
        "T T T T "
        "I I F F "
        "I I F F";

            h4{
                font-family: $font-1;
                font-size: 1.7rem !important;
                letter-spacing: 2px !important;
                    margin-top: 5px;
                grid-area: T;
                text-align: center;
            }
    
            .contact-form {
                margin: 0px auto auto 0px;
                grid-area: F;
                align-items: center;
                max-width: 50%;
                max-width: 200px;
                padding: 0 30px 10px;

            }
            .content_images{
                grid-area: I;
                
                .img{
                    margin: auto;
                    @include verticalCenter;
                    border-radius: 5px;
                    background-image: url('../../images/bryanp.jpg');
                    background-position: center;
                    background-size: cover;
                    height: 250px;
                    width: 170px;
                }
            }
            
        .form-content {
            width: 200px;
            display: grid;
            grid-template-rows: repeat(4, 1fr) 105px;

            input{
                width: 100%;
                padding: 9px 0;
                outline: none;
                margin: auto;
                background: transparent;
                border: none;  
                border-bottom: 1.3px solid black;
                font-size: .75rem;
                font-family: $font-2;
                
            }

            textarea{
                @extend input;
                margin: none;
            }
        }      
        #not-mail {
            font-size: .6rem;
            display: none;
            position: relative;
            bottom: 138px;
            color: #ff4d4d;
            left: 132px;
            transform-origin: 50% 50%;
        }
        @keyframes dongle {
            0% {
            transform: translate(0px, 0px);
            }
            10% {
            transform: translate(-10px, 0px);
            }
            20% {
            transform: translate(10px, 0px);
            }
            30% {
            transform: translate(-10px, 0px);
            }
            40% {
            transform: translate(10px, 0px);
            }
            50% {
            transform: translate(-10px, 0px);
            }
            60% {
            transform: translate(10px, 0px);
            }
            70% {
            transform: translate(-10px, 0px);
            }
            80% {
            transform: translate(10px, 0px);
            }
            90% {
            transform: translate(-10px, 0px);
            }
            100% {
            transform: translate(0px, 0px);
            }
        }
        textarea {
            resize: none;
            padding: 24px 0;
            height: 90px;
        }
        .button {
            font-family: $font-1;
            margin-top: 42px;
            font-size: 1.3rem;
            color: $white;
            background: transparent;
            border: none;
            cursor: pointer;
            transition: .2s;
            width: 100%;
        }
        .button:hover {
            letter-spacing: 2px;
        }
        .error::placeholder {
            z-index: 200000;
            color: #ff4d4d;
        }
        .form-message {
            width: 200px;
            position: relative;
            bottom: 94px;
            border-radius: 5px;
            font-size: .75rem;
            margin-top: 20px;
            opacity: 0;
            transition: 0.2s ease;
            background-color: rgb(253, 86, 86);
            color:  black;
            padding: 5px 5px;
        }
    }
}
@media (max-width: 550px){
.content_contact{
    padding: 0px 10px;

        .content{
            height: 420px;
            grid-template-areas: 
            "T T T T "
            "F F F F "
            "F F F F";
            margin: auto;
            width: 400px;
        }
        .content_images{display: none;
            
            .img{
                display: none;
            }
        }
        .form-content {
            width: 200px !important;

            input{
                font-size: .9rem !important;
                border-bottom: 2.3px solid black !important;

            }
            textarea{
                font-size: 1rem !important;
            }
            ::placeholder{
                font-size: 1em !important;
            }
        }
        h4{
            font-size: 1.7rem !important;
            letter-spacing: 2px !important;
        }

        .form-message {
            bottom: 96px !important;
        }

        .contact-form {
            margin-right: auto !important;
            margin-left: auto !important;
            width: 250px !important;
            padding-left: 0px!important;
            padding-right: 0px!important;
            padding-bottom: 0px!important;
        }
        .button {
            font-size: 1.7rem !important;
            margin-top: 39px !important; 

        }

        .img{
            width: 130px;

        }
        #not-mail {
            left: 135px !important;
        }


    }

}