header{

    h1{
        font-family: $font-1;
        font-size: 1.2rem;
    }
    min-height: 70px;
    background: $grey;
    color: honeydew;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    .logo{
        margin-left: 50px;
        text-transform: uppercase;
        letter-spacing: 2px;

    }
    ul{
        margin-right: 50px;
    
        li{
            display: inline-block;

            a{
                font-size: .6rem;
                text-transform: uppercase;
                opacity: 0.6;
                padding: 0 15px;
                color: white;
                &:hover{
                    opacity: 1;
                }
            }
        }
    }
    .menu{
        cursor: pointer;
        text-transform: uppercase;
        border: 1px solid white;
        padding: 10px 20px;
        margin-right: 20px;
        display: none
    }
    .close{
        @extend .menu;
        position: absolute;
        top: 10px;
        right: 0px;
        border-radius: 5px;
        
    }
}
section{
    height: 230px;
    background: $grey;
    .fond{
        @include verticalCenter;
        background-image: url('../../images/work.jpeg');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        width: 100%;
        height: 100%;
        opacity: 0.7;

        h2{
            
            margin: none;
            color: $white;
        }
    }
}
@media (max-width: 750px){
    header{
        .logo{
            width: 100%;
            margin: 0;
            text-align: center;
            line-height: 50px;
        }
        ul{
            width: 100%;
            margin: 0;
            text-align: center;
            line-height: 50px;

            li{
                a{
                    font-size: 1rem;
                }
            }
        }
    }
}
@media (max-width: 550px){
    header{
        h1{
            font-size: .8rem;
        }
        .toggle{
            left: 0;
        }
        ul{
            position: fixed;
            top: 0;
            left: -100%;
            background: #555;
            width: 100%;
            height: 100vh;
            display: flex;
            justify-content: space-around;
            align-items: center;
            flex-direction: column;
            z-index: 99;
            transition: 0.4s linear;
            
        }
        ul{
            li{
                line-height: normal;
            }
        }
        .menu{
            display: block;
        }
        .close{
            display: block;
        }
        .logo{
            width: initial;
            margin-left: 20px;
            line-height: 70px;
        }
    }

}
