@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Noto+Sans+JP&display=swap');$white: rgb(251, 251, 251);
$black: rgb(41, 41, 41);
$grey: rgb(199, 199, 199);

$font-1: 'Lato', sans-serif;
$font-2: 'Noto Sans JP', sans-serif;




@mixin verticalCenter{
    display: flex;
    justify-content: center;
    align-items: center;
}    

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body{
    background: $white;
}


button{
    cursor: pointer;
}
li{
    list-style: none;
}

a{
    text-decoration: none;
    color: $black;
    cursor: pointer;
}
