.footer {
  background-color: rgb(03, 03, 03);
  text-align: center;
  color: rgb(164, 158, 158);
  height: 200px;
  width: 100%;
  // background-color: rgb(190, 162, 162);
}
.cop {
  margin-top: 5px;
}
.footer_information {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 150px;
  background: #333;

  .text_email {
    cursor: pointer;
  }
  .footer_email {
    font-family: $font-2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 33vw;
    height: 25vh;
    color: white;
    font-size: 1.1rem;
    p {
      margin-top: 6px;
    }
  }
  .footer_reseaux {
    @extend .footer_email;

    :nth-child(2) {
      margin-bottom: 1px;
    }
  }
  a {
    text-decoration: none;
    color: white;
  }
}

@media (max-width: 550px) {
  .footer {

    .footer_information {
      flex-direction: column;

      .text_email {
        font-size: 0.9rem !important;
      }

      a {
        font-size: 0.9rem !important;
      }

    }
    .text_email {
      width: 0px;
    }

    .footer_email{
      height: 1px;
      width: 1px !important;
      p{
        width: 0px;
        height: 0px;
        position: relative;
        right:92px;
      }
    }
    .footer_reseaux {
      display: flex;
      flex-direction: column;
      
      :nth-child(1) {
        margin-bottom: 3px;
      }
      :nth-child(2) {
        margin-bottom: 0px;
      }
    }
  }
}