.content_timeline{
    h2{
        font-family: $font-1;
        text-align: center;
        font-size: 1.7rem;
        margin-top: 120px;
    }
}

.box-timeline {
    width: 100%;
    height: 1000px;
    background: $white;
    position: relative;
    padding: 80px 0 250px 0;
    margin-bottom: 100px;
}

.ligne {
    height: 650px;
    width: 8px;
    margin: 0 auto;
    background: #333;
}

h3{
    font-size: $font-1;
    margin-bottom: 20px;
}

.rond {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #333;
    position: absolute;
    border: 1px solid #000;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}
/* 1000 - 160 + 80 / 3 =~ 306 */
.r1{top: 80px;}
.r2{top: 386px;}
.r3{top: 692px;}
.r4{top: 1000px;}

.box {
    color: #333;
    width: 310px;
    height: 200px;
    padding: 20px;
    border-radius: 5px;
    background: $grey;
    box-shadow: 0 10px 15px rgba(0,0,0,0.5);
    position: absolute;
    left: 50%;
    // transform: translate(-50%);
    
    h3{
        font-size: $font-1;
        margin-bottom: 20px;
    }
    

}

.b1 {
    top: 80px;
    left: calc(50% - 350px);
}
.b2 {
    top: 386px;
    left: calc(50% + 37px);
}
.b3 {
    top: 692px;
    left: calc(50% - 350px);
}

.box p {
    margin-top: 10px;
    line-height: 20px;
    font-size: 14px;
    font-family: $font-2;
}

@media screen and (max-width: 700px) {

    .ligne {
        position: relative;
        left: 200px;
    }
    .rond {
        left: calc(50% + 200px);
    }
    .box {
        left: calc(50% - 150px)
    }

}
@media (max-width: 550px){
    .content_timeline{
        margin-top: 100px;
        h2{
            margin-top: 320px !important;
        }
    }
    .box-timeline{
        margin-top: 1px;
        height: 1000px;
    }
    .rond{
        display: none;
    }
     .ligne{
         display: none;
     }
     .box {
        left: calc(50% - 150px)
    }


    
}