.content_information{
    @include verticalCenter;
    height: 100vh;
    display: flex;
    flex-direction: column;

    .content{
        display: flex;
        background-color: $grey;
        border-radius: 10px;
        height: 320px;
        width: 80%;
        .content_image{
            @include verticalCenter;
            background-image: url('../../images/information.png');
            background-repeat: no-repeat;
            background-position: center;
            background-size: 80%;
            padding: 30px;
            width: 50%;
            height: 100%;
        }
        .content_text{
            line-height: 20px;
            overflow: hidden;
            padding: 30px 15px;
            color: black;
            width: 50%;
            height: 100%;
            font-family: $font-2;
            text-align: justify;
            font-size: .8rem;


        }
        a{
            font-family: $font-2;
            border-bottom: 1.3px solid black;
            text-transform: uppercase;
        }
    }
    h2{
        font-size: 1.7rem;
        font-family: $font-1;
        text-align: center;
        margin-bottom: 60px;
    
    }
}


@media (max-width: 850px){
    .content_information{

        .content{
            height: 47vh;

            .content_image{
                background-size: 90%;
            }
    
    
        }
    }
    

}

@media (max-width: 750px){

    .content_information{

    .content{
        height: 380px;

        .content_text{
            font-size: .8rem;
        }
    }
}
}

@media (max-width: 550px){
    .content_information{
        .content{
            flex-direction: column;
            height: 730px;
            
            .content_text{
                line-height: 19px;
                font-size: .85rem;
                width: 100%;
                padding: 5px 10px;
            }
            .content_image{
                width: 100%;
            }
        }
        h2{
            margin-top: 160px;
            font-size: 1.4rem;
            margin-top: 350px;

        }
    }

}
