.content_skill{
    h2{
        margin-bottom: 40px;
        font-family: $font-1;
        text-align: center;
        font-size: 1.7rem;

    }
    .content{
        height: 900px;
        background-image: url('../../images/tech.jpeg');
        background-size: cover;
        background-position: center;
        background-attachment: fixed;

        @include verticalCenter;
        flex-direction: column;
      
    }
}
.tech {
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    border-radius: 5px;
    border: ghostwhite solid 5px;
    color: rgb(255, 255, 255);
    font-size: 1.4rem;
    text-shadow: 2px 2px 4px #000000;
    text-align: center;
    width: 561.96px;
    height: 287.6px;
  
    .tech_awesome {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
  
      .competence {
        margin: 30px 40px;
      }
    }
  }
  h5{
      font-size: 3em;
      font-style: italic;
      font-family: $font-1;
      text-shadow: 2px 2px 4px #000000;
      text-align: center;
      margin: 20px;
      color: white;
      font-size: 2rem;
  
    }
  .tools {
    // margin-top: 100px;
    @extend .tech;
  }
  .tools_awesome {
    @extend .tech_awesome;
    margin-top:20px
  }
  
  .outils {
    margin: 30px 3%;
  }

  @media (max-width: 750px){
    .tech{

        font-size: 1.3rem;
        width: 500.96px;
        height: 230.6px;
    
    }

  }

  @media (max-width: 550px){
    .content_skill{

    .content{
        height: 1100px;
    }
}
    .tech{
      width: 340px !important;

        font-size: 1.3rem;
        width: 298.96px !important;
        height: 415.6px;
    
    }


  }